.page {
  justify-content: center;
  align-items: center;
  height: 100%;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
}

.center {
  z-index: 1;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
}

.hand {
  position: absolute;
}

.hand__minute, .hand__second {
  transform-origin: bottom;
  position: absolute;
}

.hand__minute {
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite move;
  top: -20vmin;
  left: -1.5vmin;
}

.hand__second {
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite move;
  top: -38vmin;
  left: -.75vmin;
}

.stopwatch--speed-up .hand__minute {
  animation-duration: 600s;
}

.stopwatch--speed-up .hand__second {
  animation-duration: 10s;
}

@keyframes move {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}
